import print from './components/print';

/**
 * @description фильтруем html и печатаем
 * */
$('.js-print').on('click', (event) => {
  const html = $('#print-area').clone();
  html.find('.no-print, .c-icon, iframe').each(function () {
    $(this).remove();
  });
  print(html.html());
  event.preventDefault();
});
